/*eslint-disable*/
import { Navbar,Container,Nav,NavDropdown } from 'react-bootstrap';
import './App.css';
import React, { useState, useEffect } from 'react';
import { Link, Route, Switch } from 'react-router-dom';
import Data from './Data';
import Detail from './Detail'
import axios from 'axios';

function App() {

  let [shoes, setShoes] = useState(Data);
  let [stock, setStock] = useState(9,11,12);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">Shoeshop</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="me-auto">
                <Nav.Link as={Link} to="/">Home </Nav.Link>
                <Nav.Link as={Link} to="/detail">Detail </Nav.Link>
                <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Switch> {/*  Switch중복 되는 조건 중 첫번째 라우트를 보여줌*/}
        <Route exact path="/"> 
          <Jumbotron/> 
          <SellList shoes={shoes} setShoes={setShoes}/>
        </Route>

        <Route path="/detail/:id">
            <Detail shoes={shoes}/>
            <Info stock={stock}/>
        </Route>

        <Route path="/:id"> {/* /뒤로 모든문자 */}
            <div>아무거나 적었을 때 이거 보여줘</div>
        </Route>
      </Switch>

    </div>
  );
}

function SellList(props) {
  return (
    <div className="container">
      {" "}
      {/* 상품리스트  */}
      <div className="row">
        {props.shoes.map((a, i) => {
          return <Product i={i} shoes={props.shoes[i]} key={i} />;
        })}
      </div>

      <button className="btn btn-primary" onClick={()=> {
        axios
        .get('https://codingapple1.github.io/shop/data2.json')
        .then((result)=>{
          console.log(result.data);
          props.setShoes([...props.shoes, ...result.data ]);
        })
        .catch(()=>{
          console.log('실패했어요')
        })
      }}>More</button>
    </div>
  );
}

function Product(props) {
  return (
    <div className="col-md-4">
      <img
        src={
          "https://codingapple1.github.io/shop/shoes" + (props.i + 1) + ".jpg"
        }
        width="100%"
      />
      <h4>{props.shoes.title}</h4>
      <p>{`${props.shoes.content} & ${props.shoes.price}`}</p>
    </div>
  );
}

function Jumbotron() {
  return (
    <div className="Jumbotron">
      <h1> 20% Season Off </h1>
      <p> WHadasd </p>
      <button> Learn more </button>
    </div>
  )
}

function Info(props) {
  return (
    <p>재고: {props.stock}</p>
  )
}

export default App;